import React, {Component} from 'react';
import axios from 'axios'
import './MemberInfo.css';
import logoutIcon from '../../img/logoutIcon.png'
import moveBtn from '../../img/stroke-1@3x.png'
import { Link, Redirect } from 'react-router-dom'
import Modal from "react-modal";

const memberInfoModalBackground = {
    overlay: {
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: 'none'
    }
}

class MemberInfo extends Component {
    constructor(props){
        super(props);

        this.state = {
            memberjoinList : [],
            redirect : false,
            redirectNum : 0
        }

    }

    componentDidMount() {
        this.handleGetJoinProject()
    }

    handleGetJoinProject = () => {
        axios.get('https://52.78.137.57/api/user/projects')
            .then(res => {
                this.setState({
                    memberjoinList: this.state.memberjoinList.concat(res.data)
                })
            })
            .catch(error => {
                console.log(error)
            })
    }

    showMemberInfoPopup = () => {
        this.props.showMemberInfoPopup()
    }

    handleLogout = () => {
        window.location = 'https://52.78.137.57/api/logout'
    }

    openMemberInfoPopup = () => {
        this.props.openMemberInfoPopup();
    }

    closeMemberInfoPopup = () => {
        this.props.closeMemberInfoPopup();
    }

    moveOtherPage = (data) => {
        window.location = 'http://localhost:3000/#/main/' + data
    }

    render(){
        // if(this.state.redirect === true){
        //     return(
        //         window.location = 'http://localhost:3000/#/main/' + this.state.redirectNum
        //     );
        //
        // }
        return (
            <Modal
                isOpen={this.props.memberInfoPopup}
                onRequestClose={this.closeMemberInfoPopup}
                className="memberInfoWrapper" style={memberInfoModalBackground}
            >
                <div className="memberInfoWrapperTitle"> </div>
                <div className="joinList">
                    {/*<div className="joinObjectWrapper" onClick={() => this.moveOtherPage(2)}>*/}
                        {/*<span className="joinObjectGisu">14</span>*/}
                        {/*<span className="joinObjectTitle"></span>*/}
                        {/*<span className="joinObjectPlatform">IOS</span>*/}
                        {/*<span className="joinObjectBtn">*/}
                                        {/*<img src={moveBtn} className="moveBtnStyled" />*/}
                                    {/*</span>*/}
                    {/*</div>*/}

                    {
                        this.state.memberjoinList.length <= 0
                            ?
                            <div className="alignCenterNoJoin">  </div>
                            :
                            this.state.memberjoinList.map((list, i) => (
                                <div className="joinObjectWrapper" key={i} onClick={() => {window.location = 'https://52.78.137.57/#/main/'+list.idx}}>
                                    <span className="joinObjectGisu">{list.orderNumber}</span>
                                    <span className="joinObjectTitle">{list.projectName}</span>
                                    <span className="joinObjectPlatform">{list.projectType}</span>
                                    <span className="joinObjectBtn">
                                        <img src={moveBtn} className="moveBtnStyled" />
                                    </span>
                                </div>
                            ))
                    }
                </div>

                <div className="logoutWrapper">
                    <img src={logoutIcon} className="logoutButton" onClick={this.handleLogout} />
                </div>
            </Modal>

        );
    }
}

export default MemberInfo;